.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0px 5px;
}

.control {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
}

.break-label {
  margin-right: 20px;
}

.session-label {
  border-left: 1px solid black;
  padding-left: 20px;
}

.controller {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
}

button {
  font-weight: bold;
  font-size: large;
  text-align: center;
  border: none;
  color: white;
  background-color: black;
  min-width: 30px;
  min-height: 30px;
  border-radius: 5px;
  cursor: pointer;
}

#session-increment,
#break-increment {
  padding-top: 3px;
}

h2 {
  margin-bottom: 25px;
  margin-top: 2px;
}

.timer-label {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
  margin-top: 20px;
}

#time-left {
  font-size: 2.2rem;
  text-align: center;
}


#start_stop {
  margin-right: 10px;
}

#reset {
  margin-left: 10px;
}